.web{
    display: flex;
    flex: 1;
    align-items: center;
}

.web-option {
    font-size: 20px;
    margin-right: 18px;
    font-weight: 700px;
}

.web-option a{
    text-decoration: none;
    color: black;
    display: flex;
    align-items: center;
}

.option-icon{
    display: flex;
    align-items: center;
    margin-right: 4px;
}

.web-option:last-child{
    margin-right: 0;
}
